@use "./blocks/variables" as*;

.banner-gift {
  position: relative;
  background: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)),
    url("../img/background-cat-gift.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 187px 60px 43px 60px;
}

.banner-gift__text {
  text-align: center;
  font-size: 16px;
  line-height: 20px;
  margin: 0;
  color: $white;
}

@media (min-width: $tablet-width) {
  .banner-gift__text {
    text-align: left;
    width: 161px;
  }

  .banner-gift {
    padding: 71px 124px 69px 453px;
  }
}


@media (min-width: $desktop-width) {
  .banner-gift {
    width: 245px;
    padding: 0;
  }
}
