@use "./blocks/variables" as*;

.field {
  box-sizing: border-box;
  border: solid 1px $border-color;

  &::placeholder {
    font-family: $oswald;
    font-size: 16px;
    text-align: left;
  }
}

.field__name {
  width: 229px;
}

.field__weight {
  width: 204px;
}

.field__age-old {
  width: 172px;
}

.field__name,
.field__weight,
.field__age-old {
  padding: 13px 0 13px 14px;
}

@media (min-width: $tablet-width) {
  .field__name,
  .field__weight,
  .field__age-old {
    padding: 10px 0 10px 18px;
    flex-grow: 1;
  }
}
